import * as winwheel from '@evshiron/winwheel.js';
import randomHex from 'random-hex';
import { useEffect, useRef, useState } from 'react';
import AddOptions from './components/AddOptions';
import './css/app.css';
import Roulette from './components/Roulette';
import Manual from './components/Manual';
import Navigation from './components/Navigation';
import getTextColorByBackgroundColor from './js/getTextColoir';

function App() {
  const rouletteRef = useRef(null);
  const [segments, setSegments] = useState(loadSegments);

  const [animation, setAnimation] = useState({
    type: 'spinToStop',
    duration: 1,
    spins: 4,
    easing: 'Power0.easeOut',
    direction: 'clockwise',
    repeat: -1,
  });
  const [spin, setSpin] = useState({
    spinning: false,
    stopping: false,
    done: false,
  });

  useEffect(() => {
    localStorage.setItem('options', JSON.stringify(segments));

    if (segments.length === 0) return;

    const roulette = new winwheel.Winwheel({
      numSegments: segments.length,
      outerRadius: 320,
      textFontSize: 16,
      responsive: true,
      lineWidth: 0,
      drawMode: 'code',
      animation,
      segments,
    });

    roulette.animation.callbackFinished = winningSegment => {
      alert(`결과: ${winningSegment.text}`);
      resetRoulette();
    };

    if (spin.spinning) {
      rouletteRef.current.startAnimation();
    }

    rouletteRef.current = roulette;
  }, [segments, animation, spin]);

  const handleAdd = text => {
    const hex = randomHex.generate();

    const newSegment = {
      fillStyle: hex,
      text,
      textFillStyle: getTextColorByBackgroundColor(hex),
      strokeStyle: 'transparent',
    };

    if (segments.length >= 25) {
      alert('선택지는 25개 이상 생성할 수 없습니다.');
      return;
    }

    setSegments(prev => [...prev, newSegment]);
  };

  const handleDelete = () => {
    const newSegments = [...segments];
    newSegments.pop();
    setSegments(newSegments);
  };

  const handleReset = () => {
    setSegments([]);
  };

  const handleStartAndStop = () => {
    if (segments.length <= 1) {
      alert('선택지는 2개 이상 작성해주세요.');
      return;
    }

    if (!spin.spinning) {
      startAndStopRoulette(
        {
          ...animation,
          repeat: 0,
          easing: 'Power3.easeOut',
          duration: 5,
        },
        { spinning: true, stopping: false, done: false },
      );
    } else {
      if (spin.done) return;

      startAndStopRoulette(
        {
          ...animation,
          repeat: -1,
          easing: 'Power0.easeOut',
          duration: 1,
        },
        { spinning: true, stopping: true, done: true },
      );
    }
  };

  function startAndStopRoulette(animation, spin) {
    setAnimation(animation);
    setSpin(spin);
  }

  function resetRoulette() {
    rouletteRef.current.stopAnimation(false);
    rouletteRef.current.rotationAngle = 0;
    setSpin({ spinning: false, stopping: false, done: false });
  }

  return (
    <div className='viewport'>
      {segments.length === 0 ? <Manual /> : <Roulette onStartAndStop={handleStartAndStop} spinStatus={spin} />}
      {!spin.spinning && (
        <>
          <AddOptions onAdd={handleAdd} />
          {segments.length > 0 && <Navigation onDelete={handleDelete} onReset={handleReset} />}
        </>
      )}
    </div>
  );
}

export default App;

function loadSegments() {
  const options = localStorage.getItem('options');
  return options ? JSON.parse(options) : [null];
}
