import React from 'react';

export default function Roulette({ onStartAndStop, spinStatus }) {
  const { spinning, stopping } = spinStatus;
  return (
    <div className='roulette'>
      <canvas id='canvas' width='600' height='600'></canvas>
      <div className={`${spinning && stopping ? 'isStop' : spinning || stopping ? 'isSpin' : 'default'} roulette__btn`}>
        <button type='button' onClick={onStartAndStop}>
          {spinning && stopping ? 'WAIT' : spinning || stopping ? 'STOP' : 'SPIN'}
        </button>
      </div>
    </div>
  );
}
