import React, { useState } from 'react';

export default function AddOptions({ onAdd }) {
  const [text, setText] = useState('');
  return (
    <form
      className='add-form'
      onSubmit={e => {
        e.preventDefault();

        if (text.trim().length !== 0) {
          onAdd(text);
          setText('');
        } else {
          window.alert('내용을 입력해 주세요.');
        }
      }}>
      <input
        type='text'
        value={text}
        onChange={e => {
          const value = e.target.value;

          if (value.length > 16) {
            alert('입력 가능한 글자 수를 초과했습니다.');
            return;
          }

          setText(value);
        }}
      />
      <button>추가</button>
    </form>
  );
}
